@keyframes rotating {
	from {
	  transform: rotate(0deg);
	}
	to {
	  transform: rotate(360deg);
	}
  }

.github-fixed-icon {
	position: fixed;
	z-index: 20;
	width: 68px;
	height: 68px;
	top: 105px;
	right: -20px;
	cursor: pointer;
	border-radius: 100%;
	line-height: 0; 
    font-size: 0;
    color: transparent; 
	background-image: url('./img/settings.png');
	background-size: cover;
	background-repeat: no-repeat;
	background-color: #8185945c;
	transition-property: all;
	transition-duration: 0.8s;
	animation: rotating 4s linear infinite;
}

.github-fixed-icon:hover {
	right: 0;
}
  