.left-block-title {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 10em;
  line-height: 1.2em;
  text-align: center;
}

.left-block.html-page {
  background: #fff;
  position: unset;
  left: 0;
  top: 0;
  width: 30vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-block.html-page {
  background: #000;
  display: flex;
  align-items: center;
}

.pointer {
  position: absolute;
  z-index: 1;
  top: -300px;
  left: -300px;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 300px;
  background: #fff;
  border-radius: 50%;
  pointer-events: none;
  mix-blend-mode: difference;
}

@media (max-width: 1150px) {
  .pointer {
    display: none;
  }
}
