.error-page {
	position: fixed;
}

.error-me {
	position: relative;
	top: 0;
	width: 100vw;
	height: 100vh;
}

.right-error-block {
	position: absolute;
	left: 0;
	width: 80%;
	height: 100vh;
	background-color: #2b1f29;
}

.error-container {
	display: flex;
	justify-content: center;
	min-height: 100vh;
	background-color: #1c2027;
  }
  
.error-container .notfound {
	position: absolute;
	left: 33%;
	top: 20%;
  }
  
  .notfound {
	max-width: 460px;
	width: 100%;
	text-align: center;
	line-height: 1.4;
  }
  
  .notfound .notfound-404 {
	height: 158px;
	line-height: 153px;
  }
  
  .notfound .notfound-404 h1 {
	font-family: 'Josefin Sans', sans-serif;
	color: #222;
	font-size: 220px;
	letter-spacing: 10px;
	margin: 0px;
	font-weight: 700;
	text-shadow: 2px 2px 0px #c9c9c9, -2px -2px 0px #c9c9c9;
  }
  
  .notfound .notfound-404 h1>span {
	text-shadow: 2px 2px 0px #ffab00, -2px -2px 0px #ffab00, 0px 0px 8px #ff8700;
  }
  
  .notfound p {
	font-family: 'Josefin Sans', sans-serif;
	color: #c9c9c9;
	font-size: 16px;
	font-weight: 400;
	margin-top: 0px;
	margin-bottom: 15px;
  }

  .notfound a, button {
	font-family: 'Josefin Sans', sans-serif;
	font-size: 14px;
	text-decoration: none;
	text-transform: uppercase;
	background: transparent;
	color: #c9c9c9;
	border: 2px solid #c9c9c9;
	display: inline-block;
	cursor: pointer;
	padding: 10px 25px;
	margin-right: 10px;
	font-weight: 700;
	transition: 0.2s all;
  }
  
  .notfound a:hover,button:hover {
	color: #ffab00;
	border-color: #ffab00;
  }
  
@media only screen and (max-width: 480px) {
	.notfound .notfound-404 {
		height: 122px;
		line-height: 122px;
	}

	.notfound .notfound-404 h1 {
		font-size: 122px;
	}
}
  
@media (max-width: 1200px) {
	.error-container .notfound {
		left: inherit;
	}
}

@media (max-width: 760px) {
	.right-error-block {
		width: 100%;
	}
	.error-container .notfound {
		top: 40px;
	}
}

@media (max-width: 320px) {
	.error-page {
		width: 320px;
	}
	
	.error-me {
		width: 320px;
		height: 100%;
	}
}